<template>
  <div class="forgot-password-container">
    <div class="container py-5">
      <div class="row justify-content-center">
        <div class="col-md-6 col-lg-5">
          <div class="card shadow">
            <div class="card-body p-5">
              <h2 class="text-center mb-4">üîë ÊâæÂõûÂØÜÁ†Å</h2>
              
              <!-- ÈîôËØØÊèêÁ§∫ -->
              <div v-if="errorMessage" class="alert alert-danger">
                {{ errorMessage }}
              </div>
              
              <!-- ÊàêÂäüÊèêÁ§∫ -->
              <div v-if="successMessage" class="alert alert-success">
                {{ successMessage }}
              </div>
              
              <!-- Ê≠•È™§1: ËæìÂÖ•ÈÇÆÁÆ±ÂíåÂèëÈÄÅÈ™åËØÅÁ†Å -->
              <template v-if="step === 1">
                <p class="text-muted mb-4">ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÊ≥®ÂÜåÈÇÆÁÆ±ÔºåÊàë‰ª¨Â∞ÜÂèëÈÄÅÈ™åËØÅÁ†ÅÂà∞ÊÇ®ÁöÑÈÇÆÁÆ±</p>
                
                <form @submit.prevent="handleSendCode">
                  <!-- ÈÇÆÁÆ± -->
                  <div class="mb-3">
                    <label for="email" class="form-label">ÈÇÆÁÆ±Âú∞ÂùÄ <span class="text-danger">*</span></label>
                    <div class="input-group">
                      <input
                        type="email"
                        class="form-control"
                        id="email"
                        v-model="formData.email"
                        required
                        :disabled="submitting || sendingCode"
                        placeholder="ËØ∑ËæìÂÖ•Ê≥®ÂÜåÈÇÆÁÆ±"
                      />
                      <button
                        type="submit"
                        class="btn btn-primary"
                        :disabled="!formData.email || sendingCode || countdown > 0"
                      >
                        <span v-if="sendingCode" class="spinner-border spinner-border-sm me-1"></span>
                        <span v-else-if="countdown > 0">{{ countdown }}s</span>
                        <span v-else>ÂèëÈÄÅÈ™åËØÅÁ†Å</span>
                      </button>
                    </div>
                    <div v-if="errors.email" class="text-danger small mt-1">
                      {{ errors.email }}
                    </div>
                    <div v-if="codeSent" class="text-success small mt-1">
                      ‚úÖ È™åËØÅÁ†ÅÂ∑≤ÂèëÈÄÅÂà∞ÊÇ®ÁöÑÈÇÆÁÆ±ÔºåËØ∑Êü•Êî∂
                    </div>
                  </div>
                  
                  <button
                    v-if="codeSent"
                    type="button"
                    class="btn btn-outline-primary w-100"
                    @click="step = 2"
                  >
                    ‰∏ã‰∏ÄÊ≠•ÔºöÈ™åËØÅÈ™åËØÅÁ†Å
                  </button>
                </form>
              </template>
              
              <!-- Ê≠•È™§2: È™åËØÅÈ™åËØÅÁ†Å -->
              <template v-if="step === 2">
                <p class="text-muted mb-4">ËØ∑ËæìÂÖ•ÂèëÈÄÅÂà∞ <strong>{{ formData.email }}</strong> ÁöÑÈ™åËØÅÁ†Å</p>
                
                <form @submit.prevent="handleVerifyCode">
                  <!-- È™åËØÅÁ†Å -->
                  <div class="mb-3">
                    <label for="verification_code" class="form-label">È™åËØÅÁ†Å <span class="text-danger">*</span></label>
                    <div class="input-group">
                      <input
                        type="text"
                        class="form-control"
                        id="verification_code"
                        v-model="formData.verification_code"
                        required
                        :disabled="submitting || verifyingCode || codeVerified"
                        placeholder="ËØ∑ËæìÂÖ•6‰ΩçÈ™åËØÅÁ†Å"
                        maxlength="6"
                      />
                      <button
                        type="submit"
                        class="btn btn-primary"
                        :disabled="!formData.verification_code || verifyingCode || codeVerified"
                      >
                        <span v-if="verifyingCode" class="spinner-border spinner-border-sm me-1"></span>
                        <span v-else-if="codeVerified">‚úì Â∑≤È™åËØÅ</span>
                        <span v-else>È™åËØÅ</span>
                      </button>
                    </div>
                    <div v-if="errors.verification_code" class="text-danger small mt-1">
                      {{ errors.verification_code }}
                    </div>
                    <div v-if="codeVerified" class="text-success small mt-1">
                      ‚úÖ È™åËØÅÊàêÂäüÔºÅ
                    </div>
                  </div>
                  
                  <div class="d-flex gap-2">
                    <button
                      type="button"
                      class="btn btn-outline-secondary"
                      @click="step = 1"
                    >
                      ËøîÂõû‰∏ä‰∏ÄÊ≠•
                    </button>
                    <button
                      v-if="codeVerified"
                      type="button"
                      class="btn btn-primary flex-grow-1"
                      @click="step = 3"
                    >
                      ‰∏ã‰∏ÄÊ≠•ÔºöËÆæÁΩÆÊñ∞ÂØÜÁ†Å
                    </button>
                  </div>
                </form>
              </template>
              
              <!-- Ê≠•È™§3: ËÆæÁΩÆÊñ∞ÂØÜÁ†Å -->
              <template v-if="step === 3">
                <p class="text-muted mb-4">ËØ∑ËÆæÁΩÆÊÇ®ÁöÑÊñ∞ÂØÜÁ†Å</p>
                
                <form @submit.prevent="handleResetPassword">
                  <!-- Êñ∞ÂØÜÁ†Å -->
                  <div class="mb-3">
                    <label for="new_password" class="form-label">Êñ∞ÂØÜÁ†Å <span class="text-danger">*</span></label>
                    <input
                      type="password"
                      class="form-control"
                      id="new_password"
                      v-model="formData.new_password"
                      required
                      :disabled="submitting"
                      placeholder="ËØ∑ËæìÂÖ•Êñ∞ÂØÜÁ†ÅÔºàËá≥Â∞ë8‰ΩçÔºâ"
                      minlength="8"
                    />
                    <div v-if="errors.new_password" class="text-danger small mt-1">
                      {{ errors.new_password }}
                    </div>
                    <small class="text-muted">ÂØÜÁ†ÅËá≥Â∞ë8‰ΩçÔºåÂª∫ËÆÆÂåÖÂê´Â≠óÊØçÂíåÊï∞Â≠ó</small>
                  </div>
                  
                  <!-- Á°ÆËÆ§Êñ∞ÂØÜÁ†Å -->
                  <div class="mb-4">
                    <label for="new_password2" class="form-label">Á°ÆËÆ§Êñ∞ÂØÜÁ†Å <span class="text-danger">*</span></label>
                    <input
                      type="password"
                      class="form-control"
                      id="new_password2"
                      v-model="formData.new_password2"
                      required
                      :disabled="submitting"
                      placeholder="ËØ∑ÂÜçÊ¨°ËæìÂÖ•Êñ∞ÂØÜÁ†Å"
                      minlength="8"
                    />
                    <div v-if="errors.new_password2" class="text-danger small mt-1">
                      {{ errors.new_password2 }}
                    </div>
                  </div>
                  
                  <!-- Êèê‰∫§ÊåâÈíÆ -->
                  <button
                    type="submit"
                    class="btn btn-primary w-100 mb-3"
                    :disabled="submitting"
                  >
                    <span v-if="submitting" class="spinner-border spinner-border-sm me-2"></span>
                    {{ submitting ? 'ÈáçÁΩÆ‰∏≠...' : 'ÈáçÁΩÆÂØÜÁ†Å' }}
                  </button>
                  
                  <button
                    type="button"
                    class="btn btn-outline-secondary w-100"
                    @click="step = 2"
                    :disabled="submitting"
                  >
                    ËøîÂõû‰∏ä‰∏ÄÊ≠•
                  </button>
                </form>
              </template>
              
              <!-- Ê≠•È™§4: ÈáçÁΩÆÊàêÂäü -->
              <template v-if="step === 4">
                <div class="text-center">
                  <div class="mb-4">
                    <i class="bi bi-check-circle-fill text-success" style="font-size: 4rem;"></i>
                  </div>
                  <h3 class="mb-3">ÂØÜÁ†ÅÈáçÁΩÆÊàêÂäüÔºÅ</h3>
                  <p class="text-muted mb-4">ÊÇ®ÂèØ‰ª•‰ΩøÁî®Êñ∞ÂØÜÁ†ÅÁôªÂΩï‰∫Ü</p>
                  <button
                    type="button"
                    class="btn btn-primary w-100"
                    @click="goToLogin"
                  >
                    ÂâçÂæÄÁôªÂΩï
                  </button>
                </div>
              </template>
              
              <!-- ËøîÂõûÁôªÂΩïÈìæÊé• -->
              <div class="text-center mt-4">
                <span class="text-muted">ÊÉ≥Ëµ∑ÂØÜÁ†Å‰∫ÜÔºü</span>
                <router-link to="/login/" class="text-decoration-none ms-1">
                  ËøîÂõûÁôªÂΩï
                </router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onUnmounted } from 'vue'
import { useRouter } from 'vue-router'
import { sendVerificationCode, verifyCode, resetPassword } from '@/api/auth'

export default {
  name: 'ForgotPasswordView',
  
  setup() {
    const router = useRouter()
    
    const step = ref(1) // 1: ËæìÂÖ•ÈÇÆÁÆ±, 2: È™åËØÅÁ†Å, 3: ËÆæÁΩÆÂØÜÁ†Å, 4: ÊàêÂäü
    const formData = ref({
      email: '',
      verification_code: '',
      verification_token: '',
      new_password: '',
      new_password2: ''
    })
    
    const errors = ref({})
    const errorMessage = ref('')
    const successMessage = ref('')
    const submitting = ref(false)
    const sendingCode = ref(false)
    const verifyingCode = ref(false)
    const codeSent = ref(false)
    const codeVerified = ref(false)
    const countdown = ref(0)
    let countdownTimer = null
    
    // ÂèëÈÄÅÈ™åËØÅÁ†Å
    const handleSendCode = async () => {
      errors.value = {}
      errorMessage.value = ''
      
      if (!formData.value.email) {
        errors.value.email = 'ËØ∑ÂÖàËæìÂÖ•ÈÇÆÁÆ±'
        return
      }
      
      // È™åËØÅÈÇÆÁÆ±Ê†ºÂºè
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
      if (!emailRegex.test(formData.value.email)) {
        errors.value.email = 'ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑÈÇÆÁÆ±Âú∞ÂùÄ'
        return
      }
      
      sendingCode.value = true
      
      try {
        await sendVerificationCode({
          email: formData.value.email,
          type: 'reset_password'
        })
        
        codeSent.value = true
        countdown.value = 60 // 60ÁßíÂÄíËÆ°Êó∂
        startCountdown()
        successMessage.value = 'È™åËØÅÁ†ÅÂ∑≤ÂèëÈÄÅÂà∞ÊÇ®ÁöÑÈÇÆÁÆ±'
        
        // Ê∏ÖÈô§ÊàêÂäüÊ∂àÊÅØÔºà3ÁßíÂêéÔºâ
        setTimeout(() => {
          successMessage.value = ''
        }, 3000)
        
      } catch (error) {
        console.error('ÂèëÈÄÅÈ™åËØÅÁ†ÅÂ§±Ë¥•:', error)
        if (error.response?.data) {
          const data = error.response.data
          if (data.error) {
            errorMessage.value = data.error
          } else if (data.email) {
            errors.value.email = Array.isArray(data.email) ? data.email[0] : data.email
          } else {
            errorMessage.value = 'ÂèëÈÄÅÈ™åËØÅÁ†ÅÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï'
          }
        } else {
          errorMessage.value = 'ÁΩëÁªúÈîôËØØÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•'
        }
      } finally {
        sendingCode.value = false
      }
    }
    
    // È™åËØÅÈ™åËØÅÁ†Å
    const handleVerifyCode = async () => {
      errors.value = {}
      errorMessage.value = ''
      
      if (!formData.value.verification_code) {
        errors.value.verification_code = 'ËØ∑ËæìÂÖ•È™åËØÅÁ†Å'
        return
      }
      
      if (formData.value.verification_code.length !== 6) {
        errors.value.verification_code = 'È™åËØÅÁ†Å‰∏∫6‰ΩçÊï∞Â≠ó'
        return
      }
      
      verifyingCode.value = true
      
      try {
        const response = await verifyCode({
          email: formData.value.email,
          code: formData.value.verification_code,
          type: 'reset_password'
        })
        
        if (response.success && response.verification_token) {
          formData.value.verification_token = response.verification_token
          codeVerified.value = true
          successMessage.value = 'È™åËØÅÊàêÂäüÔºÅ'
          
          // Ëá™Âä®ËøõÂÖ•‰∏ã‰∏ÄÊ≠•
          setTimeout(() => {
            step.value = 3
          }, 1000)
        } else {
          errors.value.verification_code = 'È™åËØÅÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•È™åËØÅÁ†Å'
        }
        
      } catch (error) {
        console.error('È™åËØÅÁ†ÅÈ™åËØÅÂ§±Ë¥•:', error)
        if (error.response?.data) {
          const data = error.response.data
          if (data.error) {
            errors.value.verification_code = data.error
          } else if (data.code) {
            errors.value.verification_code = Array.isArray(data.code) ? data.code[0] : data.code
          } else {
            errors.value.verification_code = 'È™åËØÅÁ†ÅÊó†ÊïàÊàñÂ∑≤ËøáÊúü'
          }
        } else {
          errors.value.verification_code = 'È™åËØÅÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï'
        }
      } finally {
        verifyingCode.value = false
      }
    }
    
    // ÈáçÁΩÆÂØÜÁ†Å
    const handleResetPassword = async () => {
      errors.value = {}
      errorMessage.value = ''
      
      if (!formData.value.new_password) {
        errors.value.new_password = 'ËØ∑ËæìÂÖ•Êñ∞ÂØÜÁ†Å'
        return
      }
      
      if (formData.value.new_password.length < 8) {
        errors.value.new_password = 'ÂØÜÁ†ÅÈïøÂ∫¶Ëá≥Â∞ë‰∏∫8‰Ωç'
        return
      }
      
      if (formData.value.new_password !== formData.value.new_password2) {
        errors.value.new_password2 = '‰∏§Ê¨°ËæìÂÖ•ÁöÑÂØÜÁ†Å‰∏ç‰∏ÄËá¥'
        return
      }
      
      submitting.value = true
      
      try {
        await resetPassword({
          email: formData.value.email,
          verification_token: formData.value.verification_token,
          new_password: formData.value.new_password,
          new_password2: formData.value.new_password2
        })
        
        // ÈáçÁΩÆÊàêÂäü
        step.value = 4
        
      } catch (error) {
        console.error('ÈáçÁΩÆÂØÜÁ†ÅÂ§±Ë¥•:', error)
        if (error.response?.data) {
          const data = error.response.data
          
          if (data.new_password) {
            errors.value.new_password = Array.isArray(data.new_password) ? data.new_password[0] : data.new_password
          }
          if (data.new_password2) {
            errors.value.new_password2 = Array.isArray(data.new_password2) ? data.new_password2[0] : data.new_password2
          }
          if (data.verification_token) {
            errorMessage.value = 'È™åËØÅtokenÊó†ÊïàÔºåËØ∑ÈáçÊñ∞È™åËØÅÈÇÆÁÆ±'
          }
          if (data.error) {
            errorMessage.value = data.error
          }
          
          if (!errorMessage.value && !errors.value.new_password && !errors.value.new_password2) {
            errorMessage.value = 'ÈáçÁΩÆÂØÜÁ†ÅÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï'
          }
        } else {
          errorMessage.value = 'ÁΩëÁªúÈîôËØØÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•'
        }
      } finally {
        submitting.value = false
      }
    }
    
    // ÂÄíËÆ°Êó∂ÂáΩÊï∞
    const startCountdown = () => {
      if (countdownTimer) {
        clearInterval(countdownTimer)
      }
      
      countdownTimer = setInterval(() => {
        if (countdown.value > 0) {
          countdown.value--
        } else {
          clearInterval(countdownTimer)
          countdownTimer = null
        }
      }, 1000)
    }
    
    // Ë∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µ
    const goToLogin = () => {
      router.push('/login/')
    }
    
    // Ê∏ÖÁêÜÂÆöÊó∂Âô®
    onUnmounted(() => {
      if (countdownTimer) {
        clearInterval(countdownTimer)
      }
    })
    
    return {
      step,
      formData,
      errors,
      errorMessage,
      successMessage,
      submitting,
      sendingCode,
      verifyingCode,
      codeSent,
      codeVerified,
      countdown,
      handleSendCode,
      handleVerifyCode,
      handleResetPassword,
      goToLogin
    }
  }
}
</script>

<style scoped>
.forgot-password-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 2rem 0;
}

.card {
  border: none;
  border-radius: 16px;
}

.card-body {
  background: white;
}

.alert {
  border-radius: 8px;
  border: none;
}

.btn-primary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border: none;
  font-weight: 600;
  transition: all 0.3s ease;
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);
}

.btn-primary:disabled {
  opacity: 0.7;
  transform: none;
}

.form-control:focus {
  border-color: #667eea;
  box-shadow: 0 0 0 0.2rem rgba(102, 126, 234, 0.25);
}

.text-danger {
  color: #dc3545 !important;
}

.text-success {
  color: #28a745 !important;
}
</style>

